/* box style  */
.d-flex-center-All {
    display: flex;
    justify-content: center;
    align-items: center !important;
    height: 100%;
}

.d-flex-beatwean {
    display: flex;
    justify-content: space-between;
}

.d-flex-around {
    display: flex;
    justify-content: space-around;
}

.d-flex-center {
    display: flex;
    justify-content: center;
    align-items: center !important;

}

button:focus {
    border: none !important;
    outline: none !important;
}

button,
button:active,
button:visited {
    border: none !important;
    outline: none !important;
}

/* image style  */
.img-respansive {
    max-width: 100%;
    height: auto;
    display: inline-block;
}

.img-center {
    height: auto;
    width: 40%;
}

@media screen and (max-width:540px) {
    .img-center {
