}


/* text graphy  */
.bold {
    font-weight: bolder;
}

.noSelect {
    -webkit-user-select: none;
    user-select: none;
}

.cl-gray {
    color: #87868a;
}

.ct {
    color: #397ab7 !important;
}

.eventColor {
    color: #FE4C24;
}

/* events mouse */
.pointer {
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
}

/* icons  */
.IconSet span {
    padding-right: 5px;
}

.IconSet {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* animation sets */
.Effect:active {
    animation-name: bounceIn;
    animation-duration: 2s;

}

.effect {
    animation-name: bounceOut;
    animation-duration: 2s;
}

.unload {
    animation-name: bounceOut;
    animation-duration: 2s;
}


/* scrolls  */
*::-webkit-scrollbar {
    background-color: #fff;
    width: 16px;
    border: 4px solid #fff;
}

*::-webkit-scrollbar {
    width: 16px;
}

*::-webkit-scrollbar-thumb {
    background-color: #babac0;
    border-radius: 16px;
    border: 5px solid #fff;
    width: 2px !important;
}

.no-scroll::-webkit-scrollbar {
    visibility: hidden;
}

.noSelect::-webkit-scrollbar-thumb {
    background-color: #babac0;
    border-radius: 16px;
    border: .1px solid #babac0;
    width: 1px !important;
}

