@keyframes bounceIn {
    0% {
        transform: scale(1);

    }

    100% {
        transform: scale(0.8);
    }
}

@keyframes bounceOut {
    0% {
        transform: scale(0.8);

    }

    100% {
        transform: scale(1);
    }
}

